import logo from './logo.svg';
import './App.css';
import "../node_modules/bootstrap/dist/css/bootstrap.min.css";
import "../node_modules/bootstrap/dist/js/bootstrap.bundle";
import {useState} from 'react';
import db from './fire';

function App() {

const [firstname,setfirstname]=useState();
const [lastname,setlastname]=useState();

const handlesubmit = (e) =>{
e.preventDefault();

db.collection("contacts").add({
firstname:firstname,
lastname:lastname,

})

.then(() => {
  alert("message is submitted")
})

.catch((error)=>{
alert(error.message);

})

setfirstname("");
setlastname("") ;

};
  

return (
    <>




<form onSubmit={handlesubmit}>
  <div class="mb-3">
    <label for="exampleInputEmail1" class="form-label">first name</label>
    <input type="text" class="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" name={firstname} onChange={e => setfirstname(e.target.value)}/>
    
  </div>
  <div class="mb-3">
    <label for="exampleInputPassword1" class="form-label">last name</label>
    <input type="text" class="form-control" id="exampleInputPassword1" name={lastname} onChange={e => setlastname(e.target.value)}/>
  </div>
  
  <button type="submit" class="btn btn-primary" >Submit</button>
</form>




    </>
  );
}

export default App;
